nav{
    background:rgba(0,0,0,0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover{
    box-shadow:  3px 3px 10px var(--color-primary);
    background: rgba(0,0,0,0.3);

}
nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}
/* ================= MEDIA QUERY ===================*/

/*------Medium device----*/
@media screen and (max-width: 1024px){
   
 }
 
 /*------Small device----*/
 @media screen and (max-width: 600px){
  nav{
    
    display: block;
    padding: 0.2rem 0rem;
    z-index: 1;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    width: max-content;
  }
 }