header{
    height: 70vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*======= CTA =========*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*======= Social icons =========*/

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    font-size: 2.5rem;
    left: 0;
    bottom: -1rem;
}

/*.header_socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}
*/
/*======= Image =========*/

.me{
    
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    
}

/*-========== scroll down====*/
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;

}

/* ================= MEDIA QUERY ===================*/

/*------Medium device----*/
@media screen and (max-width: 1024px){
   header{
       height: 40vh;
       
   }
   
   
}

/*------Small device----*/
@media screen and (max-width: 600px){
   header{
       height: 50vh;
       padding: 1rem;
       
   }
  
   .header_socials,
   .scroll_down{
       display: none;
   }
}